  height: 300px;
  background-color: #282c34;
}

h1{
  position: absolute;
  font-family: 'Righteous';
  font-size: 50px;
  color:white;
  margin-top: 2px;
  cursor: pointer;
}

h3{
  font-family: 'Poppins', sans-serif;
  
    font-size: 20px;

}
h6{
  font-family: 'Open Sans', sans-serif;
}
.swipe {
  position: absolute;
/* background-color: red; */
}

.cardContainer {
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-end;
  align-items: center;
  margin-top: 100px;
  cursor: grab;
}
.cardContainer:active{
  cursor: grabbing;
}

.gif {
  border-radius: 8px;
  display: flex;
  /* margin-bottom: auto; */
  margin-left: auto;
  margin-right: auto;
  margin-top: 5px;
  width: 50%;
  height: 32%;
  overflow: hidden;
}

.card {
  position: relative;
  /* display: flex;
  flex-direction: column;
  justify-content: space-evenly; */
  background-color: rgb(255, 255, 255);
  /* width: 55vw; */
  max-width: 260px;
  /* height: 100vw; */
  height: 400px;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 4px 12px;
  border-radius: 15px;
  overflow-wrap: break-word;
  padding: 20px;
  
}

